export default {
    // refactor -> replace jQuery deferred with Promise api
    toggleAnimationFor: function ( el, animName ) {
        var dfd = $.Deferred();
        // ensure jQuery node
        el = ( el instanceof $ && el[0] ) ? el : $( el );

        if ( el && Modernizr.cssanimations ) {
            el.on( upi.prefixes.animEndEventName, function cleanup() {
                el.removeClass( animName );
                el.off( upi.prefixes.animEndEventName, cleanup );
                dfd.resolve();
            } );
            el.addClass( animName );
        }
        else {
            dfd.resolve();
        }

        return dfd;
    },

    toggleTransitionFor: function ( el, className ) {
        var dfd = $.Deferred();
        // ensure jQuery node
        el = ( el instanceof $ && el[0] ) ? el : $( el );

        if ( el && Modernizr.cssanimations ) {
            el.on( upi.prefixes.transEndEventName, function cleanup() {
                el.removeClass( className );
                el.off( upi.prefixes.transEndEventName, cleanup );
                dfd.resolve();
            } );
            el.addClass( className );
        }
        else {
            dfd.resolve();
        }

        return dfd;
    }

}



