import contains from 'lodash.contains';
import orderUtil from './orderUtil';
import OrderItemController from './orderItemController';
import View from '../core/View';
import OrdersService from '../../services/OrdersService';

var ORDER_PROCESS_ERROR_MSG = `We're sorry, there was a problem while attempting to process this order`;

/**
 * OrderItemPendingController
 * Manages pending order item on the page that have a data-order-guid attached to it
 * Delgates order approval status, PO# updates nad triggers change events to the document
 * @requires OrderItemController
 * @el {HTMLElement} - the order item container
 */

export default OrderItemController.extend( {

    defaults: {
        request: OrdersService.getOrderDetails
    },

    currentStatus: null,

    elements: {
        '.accordion-content': 'contentArea',
        '.odr-po': 'pofieldWrapper',
        '.odr-po input': 'pofield',
        '.odr-notes-field--po input[type="text"]': 'pofieldAlt',
        '.odr-actions': 'orderActions',
        '.odr-status': 'orderStatusBadge',
        '[id^="customernotes-fieldset-"]': 'messageFieldSet',
        '[id^="rejectnotes-"]': 'messageField'
    },

    events: {
        // Caveat - current implementation does not inherit event object. Events from parent must be inherited manually
        // parent
        'click .print-invoice': 'handlePrintInvoice',
        'contenteditable:saved': 'handlePONumber',
        'keypress pofieldAlt': 'onKeypress',
        'keypress messageField': 'onKeypress',
        // extend
        'click .odr-actions .button': 'handleOrderAction',
        'orderstatus:change': 'updateOrderStatusBadge'
    },

    init: function ( el, options ) {

        this.options = this.options || {};
        Object.extend( this.options, this.defaults, options || {} );

        this._super( el, this.options );

        if ( !this.guid ) {
            return;
        }

        this.el = $( el );
        this.isPendingOrderView = !!this.el.parents( '.adm-pending-orders' )[0];
        this.currentProcess = null;
        this.orderApprovalDialogView = new View( 'lgpOrderApprovalDialog' );
        this.orderStatusBadgeView = orderUtil.orderStatusBadgeView;
        this.messageboxView = new View( 'messagebox' );
        this.messageboxWrap = $( '<div/>' );
        this.fieldSetMessagebar = $( '<div class="order-action-message"/>' );
        this.update_elements();

        // patch - remove server rendered messageFields due to PO# update
        // todo - remove server rendered message fields
        if ( this.messageFieldSet && this.messageFieldSet[0] ) {
            this.messageFieldSet.remove();
        }

        // add message dialog containers
        this.orderApprovalDialogView.whenTemplatesLoaded.then( () => {
            var orderApprovalDialog = this.orderApprovalDialogView.render( {
                    guid: this.guid,
                    poNumber: this.pofield.val()
                } ),
                wrapper = $( '<div class="hidden">' );

            this.el.append( wrapper.html( orderApprovalDialog ) );
            this.update_elements();

            // ensure dialog pofield gets updated when contenteditable field changes
            this.bind( 'pofield:update', ( e, {guid, ponumber} ) => {
                if ( guid === this.guid ) {
                    this.pofieldAlt.val( ponumber );
                }
            } );

            this.messageFieldSet.prepend( this.fieldSetMessagebar );
        } );

        // bind reset events
        $( document ).on( 'orderitemapproval:off', this.resetDialog.bind( this ) );
    },

    handleAccordionToggle: function ( e ) {
        this._super( e );
        this.resetDialog();
    },

    handleOrderAction: function ( e ) {
        e.preventDefault();
        e.stopPropagation();

        var self = this,
            $target = $( e.currentTarget ),
            action = $target.attr( 'data-action' ),
            targetIsApprovalStatusButton = contains( [
                'reject', 'approve'
            ], action ),
            $orderActions = $target.parents( '.odr-actions' );

        if ( self.currentProcess ) {
            return;
        }
        if ( targetIsApprovalStatusButton ) {
            this.currentStatus = action;
        }

        this.clearMessageBar();
        if ( 'submit' === action ) {
            this.submitForm( $target );
        }
        else {
            $( document ).trigger( 'orderitemapproval:off' );
            this.toggleApprovalDialog( $orderActions, targetIsApprovalStatusButton );
        }
    },

    toggleProcessing: function () {
        this.orderActions.toggleClass( 'is-processing', !!this.currentProcess );
    },

    /**
     * submitForm:
     * handle the submission/errors for form and always clear out currentProcess state
     */
    submitForm: function () {
        this.currentProcess = this.updateOrderStatus( this.currentStatus );

        if ( this.currentProcess.state() !== 'rejected' ) {
            this.toggleProcessing();
            this.setStatusMessage( 'processing', 'Submitting...' );
        }

        this.currentProcess.then( function () {
            $( document ).trigger( 'orderitemapproval:off' );
        } ).always( function () {
            delete this.currentProcess;
            this.toggleProcessing();
        }.bind( this ) );

        return false;
    },

    onKeypress( e ) {
        if ( e.which === 13 ) {
            return this.submitForm();
        }
    },

    updateOrderStatus: function ( status ) {
        var self = this,
            // refactor - replace $.Deferred object with native Promise
            dfd = $.Deferred(),
            req;

        if ( 'approve' === status ) {
            let ponumber = this.pofieldAlt.val();

            // if po# field invalid reject promise
            // else make sure the PO number is updated on the server
            req = (!ponumber)
                ? Promise.reject( 'Please provide a PO#' )
                : this.updatePONumber( ponumber ).then( () => {
                    return this.approveOrder();
                } );
        }
        else if ( 'reject' === status ) {
            req = this.rejectOrder( this.messageField.val() );
        }

        req.then(
            function ( resp ) {
                if ( 'success' === resp.Result.Status.toLowerCase() ) {
                    if ( self.isPendingOrderView ) {
                        self.closeOrderItem( status );
                        $( '#body-content' ).scrollToElement( {effect_speed: 300} );
                    }
                    else {
                        self.setOrderStatus( status );
                        self.el.trigger( {type: 'close:accordion', item: self.el} ).scrollToElement(); // account for fixed header;
                    }

                    self.removePOField();
                    dfd.resolve();
                }
                else {
                    self.setStatusMessage( 'error', ORDER_PROCESS_ERROR_MSG );
                    dfd.reject();
                }
            },
            function () {
                var msg = arguments[arguments.length - 1];
                self.setStatusMessage( 'error', msg || ORDER_PROCESS_ERROR_MSG );
                dfd.reject();
            }
        );

        return dfd;
    },

    setOrderStatus: function ( status ) {
        this.el.attr( 'data-order-status', status ).trigger( 'orderstatus:change', status );
    },

    updateOrderStatusBadge: function ( e, status ) {
        var newBadgeHtml = orderUtil.renderStatusBadge( status );
        this.orderStatusBadge.replaceWith( newBadgeHtml );
    },

    closeOrderItem: function ( status ) {
        var self = this,
            el = this.el;

        el.slideUp( 250, function () {
            el.remove();
            self.updatePriorityHeaders();
            self.changePendingCountBy( 1 );
            if ( 'approve' === status ) {
                self.changeApprovedCountBy( 1 );
            }
        } );

        self.destroy();
        $( document ).trigger( 'orderitem:closed' );

        return this.el;
    },

    updatePriorityHeaders: function () {
        $( '.accordion-division' ).each( function () {
            if ( !$( this ).next().hasClass( 'accordion-item' ) ) {
                $( this ).slideUp( 200 );
            }
        } );
    },

    setStatusMessage: function ( type, message ) {
        var messageView = this.messageboxView.render( {
            type: type || '',
            message: message || ''
        } );
        this.fieldSetMessagebar.html( messageView );
    },

    approveOrder: function () {
        return OrdersService.approveOrder( {
            orderGuid: this.guid,
            adminNotes: ''
        } );
    },

    rejectOrder: function ( message ) {
        message = message || '';
        return OrdersService.rejectOrder( {
            orderGuid: this.guid,
            adminNotes: message.trim()
        } );
    },

    toggleApprovalDialog: function ( target, isApproval ) {
        if ( isApproval ) {
            this.showMessageBox( this.currentStatus );
        }

        target.prepend( this.messageFieldSet )
            .attr( 'data-action-state', isApproval ? 'notes' : 'approval' )
            .attr( 'data-dialog-status', this.currentStatus );
    },

    showMessageBox: function ( status ) {
        var self = this;

        var positionTo,
            isApprove = 'approve' === status,
            message = this.messageboxView.render( {
                type: isApprove
                    ? 'success'
                    : 'notice',
                message: isApprove
                    ? 'Approving order'
                    : 'Rejecting order'
            } );

        this.messageFieldSet.prepend( this.messageboxWrap.html( message ) );
        positionTo = this.el.hasClass( 'active' )
            ? 'bottom'
            : 'top';
        this.scrollToOrderItem( positionTo );
        setTimeout( function () {
            self.messageField.focus();
        }, 0 );
    },

    resetDialog: function () {
        this.clearMessageBar();
        this.orderActions.attr( 'data-action-state', 'approval' );
    },

    clearMessageBar: function () {
        this.fieldSetMessagebar.empty();
    },

    changePendingCountBy: function ( count ) {
        $( document ).trigger( 'orderitempending:change', {count: count} );
    },

    changeApprovedCountBy: function ( count ) {
        $( document ).trigger( 'orderitemapproved:change', {count: count} );
    },

    destroy: function () {
        this.el.off( 'click' );
        this.el.off( 'contenteditable:saved' );
    }

} );



