import ServiceProvider from '../modules/core/ServiceProvider';
import User from '../modules/User';

const svc = new ServiceProvider( {
    baseURL: upi.conf.svc.ECOMMERCE_API
} );

const OrdersService = {
    /**
     * Get recommended items based on users recent purchases
     * @param {Number} resultItemLimit - max number of results to return
     * @param {Number} months - number of months back to include in query
     * @param {Boolean} filterCart - filter items that are currently in cart
     * @returns {*}
     */
    getPastPurchases( resultItemLimit = 12, months = 9, filterCart = true ) {
        if ( !User.isLogged() ) {
            return Promise.reject( 'User is not logged in' );
        }

        return svc.get( '/getSuggestedItems', {
            cache: true,
            cacheForUser: true,
            params: {
                months,
                resultItemLimit,
                filterCart
            }
        } ).then( ServiceProvider.responseData );
    },

    getOrderHistory( params ) {
        return svc.get( '/getOrdersNoLinesJson', {params} )
            .then( ServiceProvider.responseData )
    },

    getOrderDetails( params ) {
        return svc.get( '/getOrderHtml',
            {
                responseType: 'text',
                cache: true,
                params
            } )
            .then( ServiceProvider.responseData )
    },

    getOrderDetailsAndTracking( params ) {
        return svc.get( '/getOrderPlusTrackingHtml',
            {
                responseType: 'text',
                cache: true,
                params
            } )
            .then( ServiceProvider.responseData )
    },

    updateOrderPO( params ) {
        if ( !params.orderGuid ) {
            throw new Error(
                'upi.OrdersService:updateOrderPO requires an "orderGuid" param'
            );
        }
        if ( !params.PONumber ) {
            throw new Error(
                'upi.OrdersService:updateOrderPO requires an "PONumber" param'
            );
        }
        return svc.post( '/updateOrderPONumberJson', {params} )
            .then( ServiceProvider.responseData )
    },

    approveOrder( params ) {
        if ( !params.orderGuid ) {
            throw new Error(
                'upi.OrdersService:updateOrderPO requires an "orderGuid" param'
            );
        }

        return svc.post( '/approveOrderJson', {params} )
            .then( ServiceProvider.responseData )
    },

    rejectOrder( params ) {
        if ( !params.orderGuid ) {
            throw new Error(
                'upi.OrdersService:updateOrderPO requires an "orderGuid" param'
            );
        }

        return svc.post( '/rejectOrderJson', {params} )
            .then( ServiceProvider.responseData )
    }
};

export default OrdersService;



